import styled, {css} from 'styled-components';
import {npcColumns} from '../commons/list';
import {backendURL} from '../commons/const';


import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import {red, blue, brown, green} from '@material-ui/core/colors';

export default (props) => {
  const {id, onClose} = props;
  
  if(!id) return null;

  return (
	<Wrapper bg={props.bg || 'white'} borderRadius="8px" borderColor="rgba(0,0,0,.1)" border="1px solid">
    <Button onClick={onClose}>Close</Button>
    <Image src={backendURL + '/static/images/' + id + '.jpg'} />
	</Wrapper>
)}

const Wrapper = styled.div`
  left: 5%;
  padding: 20px;
  position: fixed;
  top: 0;
  height: 50%;
  width: 50%;
  z-index: 99999999;
  background: #222;
  border: 1px solid #eee;
  overflow: auto;
  color: #fff;
`


const Image = styled.img`
  display: block;
  width: 100%;
  height: auto;
`
